/* NAV ------------------------------------------------------------------------------------------------------------------------------------------ */
nav {
    display: flex;
    height: 8.5vh;
    padding: 1vh 0;
    position: fixed;
    top: 0;
    background: #fff;
    z-index: 9999;
    box-shadow: 0px 1px 25px rgba(0, 0, 0, 0.08);
}
#short-nav-logo {
    display: none;
}

.nav-left,
.nav-right {
    height: 100%;
    display: flex;
}

.nav-left {
    width: 80%;
}

.nav-right {
    width: 20%;
    display: flex;
    justify-content: flex-end;
    align-items: center;
}

.nav-logo-holder {
    width: 12%;
    height: 100%;
    margin-right: 3vw;
    display: flex;
    justify-content: center;
    align-items: center;
}

.nav .logo {
    width: 100%;
}

.nav-links {
    display: flex;
    align-items: center;
    transition: all 0.3s ease-in-out;
}

.nav-link {
    text-decoration: none;
    color: #000;
    transition: all 0.3s;
    margin-right: 2vw;
    font-size: 1vw;
    position: relative;
    font-weight: 400;
}

.nav-link::after {
    content: "";
    position: absolute;
    bottom: -4px;
    left: 50%;
    -webkit-transform: translateX(-50%);
            transform: translateX(-50%);
    width: 0;
    height: 2.8px;
    border-radius: 50px;
    background: #4678f9;
    transition: all 0.2s;
}

.nav-link:hover {
    color: #4678f9;
}

.nav-link:hover:after {
    width: 60%;
}

.nav-link-active {
    color: #4678f9;
}

.nav-link-active:after {
    width: 80%;
}

.disc-btn {
    text-decoration: none;
    border: 1px #4678f9 solid;
    color: #4678f9;
    border-radius: 3px;
    font-size: 0.98vw;
    padding: 0.6vh 1.6vw;
    transition: all 0.2s;
}
.disc-btn:hover {
    background: #4678f9;
    color: #fff;
}
.login-btn {
    text-decoration: none;
    background: #4678f9;
    color: #fff;
    border: none;
    cursor: pointer;
    border-radius: 3px;
    font-size: 0.98vw;
    padding: 0.6vh 1.6vw;
    transition: all 0.3s ease-in-out;
}
.login-btn:hover {
    background: #4371e6;
}

.ham {
    padding: 2vh 2vw;
    cursor: pointer;
    display: none;
    z-index: 100;
}
.ham .line {
    width: 4.6vw;
    height: 3px;
    background: #29313d;
    border-radius: 5px;
    position: relative;
    transition: all 0.2s;
}
.line::before,
.line::after {
    content: "";
    position: absolute;
    left: 0;
    width: inherit;
    height: inherit;
    background: #29313d;
    border-radius: inherit;
    transition: inherit;
}
.line::before {
    top: -0.9vh;
}
.line::after {
    top: 0.9vh;
}

.ham-active .line {
    background: transparent;
}
.ham-active .line::after {
    top: 0;
    -webkit-transform: rotate(45deg);
            transform: rotate(45deg);
}
.ham-active .line::before {
    top: 0;
    -webkit-transform: rotate(-45deg);
            transform: rotate(-45deg);
}

.pfp {
    border-radius: 50%;
    height: 2vw;
    width: 2vw;
    transition: all 0.3s ease-in-out;
}
.logout {
    display: flex;
    align-items: center;
    justify-content: center;
    margin-left: 1.6vw;
    background: none;
    cursor: pointer;
    transition: all 0.3s ease-in-out;
}
.logout img {
