}

.notif-card {
    width: 20vw;
    height: 26vw;
    background: #FFFFFF;
    box-shadow: 0px 19.847px 138.929px rgba(0, 0, 0, 0.25);
    border-radius: 3px;
    padding-left: 1vw;
    padding-right: 0.5vw;
    padding-top: 1vw;
    margin-top: 3.2vw;
    margin-right: 15vw;
    position: fixed;
    z-index: 999999;
    right: 0;
}

.notif-card p {
    font-size: 1.1vw;
    font-weight: 500;
}

.notif-card-body {
    margin-top: 0.5vw;
    overflow: auto;
    height: 90%;
    width: 100%;
    scrollbar-width: 0.1vw;
}

.notif-card-body::-webkit-scrollbar-track {
    background: #f5f5f5;
    border-radius: 8px;
}

.notif-card-body::-webkit-scrollbar-thumb {
    background-color: #747474;
    border: 0px solid transparent;
    border-radius: 8px;
    background-clip: padding-box;  
  }
  
.notif-card-body::-webkit-scrollbar {
    width: 6px;
}

.notif {
    display: flex;
    flex-flow: row wrap;
    align-items: center;
    padding-bottom: 0.5vw;
}

.notif hr {
    width: 93%;
    border: 0.1px solid #C4C4C4;
    margin-top: 0.5vw;
    margin-bottom: -0.3vw;
    opacity: 0.4;
}

.notif .left {
    width: 20%;
    padding-top: 0.8vw;
    /* background-color: #4371e6; */
}

.notif .left img {
    width: 3.5vw;
    height: auto;
    border-radius: 3px;
}

.notif .right {
    width: 77%;
    /* background-color: #85d49c; */
    padding-left: 1vw;
    font-weight: normal;
}

.notif .right #time {
    color: #747474;
    font-size: 0.7vw;
    margin-top: 0.2vw;
}

.notif .right p {
    font-size: 0.8vw;
}

#no-notifs {
    color: #747474;
    font-size: 1vw;
    margin-top: 0.2vw;
}

/* MEDIA */
@media (max-width: 1100px) {
    .container {
        padding: 0 6vw;
    }
    .nav-link,
    .disc-btn,
    .login-btn {
        font-size: 1.2vw;
    }
    .nav-link {
        margin-right: 2.4vw;
    }
    .pfp {
        height: 3.2vw;
        width: 3.2vw;
    }
    .logout img {
        width: 2vw;
    }
}
@media (max-width: 800px) {
    .container {
        padding: 0 5vw;
    }
    .nav-links {
        position: fixed;
        width: 45vw;
        height: 100vh;
        background: #fff;
        top: 0;
        right: 0;
        z-index: 10;
        display: flex;
        justify-content: center;
        align-items: center;
        flex-direction: column;
        -webkit-transform: translate3d(100%, 0, 0);
                transform: translate3d(100%, 0, 0);
    }
    .nav-links-active {
        -webkit-transform: translate3d(0, 0, 0);
                transform: translate3d(0, 0, 0);
        box-shadow: 0 0 100px #29313d56;
    }
    .nav-link,
    .disc-btn,
    .login-btn {
        margin: 2vh 0;
        font-size: 2vh;
    }
    .disc-btn,
    .login-btn {
        padding: 1vh 5vw;
    }
    .login-btn {
        position: fixed;
        bottom: 2%;
        right: calc(45vw / 2);
        -webkit-transform: translate3d(300%, 0, 0);
                transform: translate3d(300%, 0, 0);
        z-index: 11;
    }
    .login-btn-active {
        -webkit-transform: translate3d(50%, 0, 0);
                transform: translate3d(50%, 0, 0);
        box-shadow: 0 0 100px #29313d56;
    }
    nav {
        height: 7vh;
    }
    .nav-logo-holder {
        display: none;
    }
    #short-nav-logo {
        display: flex;
    }
    #short-nav-logo img {
        width: 60%;
    }
    .ham {
        display: inline-block;
    }
    .pfp {
        height: 8vw;
        width: 8vw;
        position: fixed;
        bottom: 8%;
        right: calc(45vw / 2);
        -webkit-transform: translate3d(550%, 0, 0);
                transform: translate3d(550%, 0, 0);
        z-index: 11;
    }
    .pfp-active {
        -webkit-transform: translate3d(50%, 0, 0);
                transform: translate3d(50%, 0, 0);
        box-shadow: 0 0 100px #29313d56;
    }
    .logout {
        position: fixed;
        bottom: 2%;
        right: calc(45vw / 2);
        -webkit-transform: translate3d(860%, 0, 0);
                transform: translate3d(860%, 0, 0);
        z-index: 11;
    }
    .logout img {
        width: 4vw;
    }
    .logout-active {
        -webkit-transform: translate3d(50%, 0, 0);
                transform: translate3d(50%, 0, 0);
    }
}
@media (max-width: 500px) {
    #short-nav-logo img {
        width: 100%;
    }
    .ham .line {
        width: 6.6vw;
    }
    .nav-links {
        width: 80vw;
    }
    .login-btn {
        right: calc(80vw / 2);
    }
    .disc-btn,
    .login-btn {
        padding: 0.8vh 8vw;
    }
    .pfp {
        height: 15vw;
        width: 15vw;
        right: calc(80vw / 2);
    }
    .pfp-active {
        -webkit-transform: translate3d(50%, 0, 0);
                transform: translate3d(50%, 0, 0);
        box-shadow: 0 0 100px #29313d56;
    }
    .logout {
        right: calc(80vw / 2);
    }
    .logout img {
        width: 7vw;
    }
    .logout-active {
        -webkit-transform: translate3d(50%, 0, 0);
                transform: translate3d(50%, 0, 0);
    }
}

