    height: calc((35 / 100 * 100vw) * 1.5);
    border-radius: 50%;
    -webkit-transform: translateY(-20%);
            transform: translateY(-20%);
    -webkit-animation: rot 8s infinite linear;
            animation: rot 8s infinite linear;
}

