    position: absolute;
    width: 12vw;
    height: 12vw;
    background: #4678f9;
    border-radius: 50%;
    display: flex;
    justify-content: center;
    align-items: center;
    color: #fff;
    animation: rot2 8s infinite linear reverse;
}

