    width: 3vw;
    height: 3vw;
    border-radius: 50%;
}

.add-comm-top textarea {
    width: 100%;
    min-height: 10vh;
    resize: vertical;
    padding: 2vh 1vw;
    border: 1px solid #c4c4c4;
    border-radius: 5px;
    font-size: 0.9vw;
}

.comment-btns {
    display: flex;
    justify-content: flex-end;
    margin-top: 3vh;
}

.comment-btns .btn {
    padding: 1vh 2vw;
    font-size: 0.7vw;
    background: transparent;
    border: none;
    cursor: pointer;
    margin-left: 1vw;
    border-radius: 5px;
    transition: all 0.3s;
}

.comment-btns .com-cancel {
    border: 1px solid #29313d;
    color: #29313d;
}

.comment-btns .com-create {
    background: #4678f9;
    color: #fff;
}

.comment-btns .com-cancel:hover {
    background: #29313d;
    color: #fff;
}

.comment-btns .com-create:hover {
    background: #2e57be;
}

/* Comment --------------------------------------------------------------------------- */

.comment {
    display: flex;
    margin-bottom: 5vh;
    flex-wrap: wrap;
}

.comm-icon {
    height: 100%;
}

.comm-content {
    display: flex;
    flex-direction: column;
}

.comment .details {
    display: flex;
    align-items: center;
    margin-bottom: 1vh;
}

.comment .details h4,
.comment .details p {
    text-decoration: none;
    font-size: 0.9vw;
}

.comment .details h4 {
    color: #4678f9;
    font-weight: 400;
}

.comment .details p {
    margin-left: 1vw;
    color: #747474;
}

.comm-body {
    text-align: justify;
    margin-bottom: 1.4vh;
}
.comm-card-opts {
    width: 100%;
    display: flex;
    justify-content: flex-end;
}
#delete-comment {
