    opacity: 0.7;
    font-size: 0.9vw;
    color: #ff6b6b;
    margin-left: 2vw;
    transition: all 0.3s;
}
#delete-comment:hover {
    opacity: 1;
}

/* Modal --------------------------------------------------------------------------- */

.ReactModal__Html--open,
.ReactModal__Body--open {
    overflow: hidden;
    /* prevents background page from scrolling when the modal is open */
}

.ReactModal__Overlay {
    position: fixed;
    z-index: 1;
    top: 0;
    left: 0;
    width: 100vw;
    height: 100vh;
    background: rgba(0, 0, 0, 0.5);
    display: flex;
    align-items: center;
    justify-content: center;
}

.ReactModal__Content {
    background: white;
    width: 70vw;
    border-radius: 16.9046px;
    box-shadow: 0 0 30px 0 rgba(0, 0, 0, 0.25);
    position: relative;
    padding: 5vh 4vw;
}

.modal-content {
    height: 100%;
    width: 100%;
    overflow-y: auto;
}

.modal-content .header {
    align-items: center;
    display: flex;
    width: 100%;
}

.modal-content .left {
    margin-left: 0vw;
    height: auto;
    width: 70%;
}

.left h1 {
    font-style: normal;
    font-weight: 600;
    font-size: 2vw;
}

.left p {
    font-style: normal;
    font-weight: normal;
    font-size: 1vw;
    color: #9c9c9c;
    margin-top: 0vw;
}
.modal-content .header h2 {
    font-size: 1.2vw;
    font-weight: 400;
    text-decoration: underline;
    color: #4678f9;
    margin-top: 1.6vh;
    width: 30%;
    text-align: right;
}

.input-fields {
    margin-top: 1.8vw;
    display: flex;
    flex-direction: column;
}

.input-fields label {
    font-size: 1vw;
}

.input-fields input {
    border: none;
    width: 100%;
    border: 1px solid #c4c4c4;
    box-sizing: border-box;
    border-radius: 5px;
    padding: 1vh 1vw;
    font-size: 1vw;
    margin-top: 0.2vh;
}

.textbox-wrapper {
    width: 100%;
    margin-top: 2.4vh;
    z-index: 999999;
}

.buttons {
    align-items: center;
    display: flex;
    margin-top: 2.4vh;
    margin-bottom: 3vh;
}

.create-post {
    display: flex;
    justify-content: center;
    align-items: center;
    font-size: 0.9vw;
    padding: 1vh 1.6vw;
    font-weight: 300;
    border-radius: 5px;
    background: #4678f9;
    border: 1px solid #4678f9;
    transition: all 0.3s;
    cursor: pointer;
    color: white;
}

.save-draft {
    display: flex;
    height: 100%;
    justify-content: center;
    align-items: center;
    font-size: 0.9vw;
    padding: 1vh 1.6vw;
    font-weight: 300;
    border-radius: 5px;
    background: transparent;
    transition: all 0.3s;
    cursor: pointer;
    border: 1px solid #29313d;
    box-sizing: border-box;
    border-radius: 4.28558px;
    margin-left: 1vw;
}

i {
    margin-top: 0.4vw;
}

.post-content p {
    margin-bottom: 0vh;
}

