    height: 2vw;
    display: flex;
    justify-content: center;
    align-items: center;
    /* background: #29313d; */
    color: #4678f9;
    border-radius: 7px;
    margin: 0 0.7vw;
    transition: all 0.3s;
}
.page-active {
    background: #4678f9;
    color: #fff;
}
/* .page:hover {
    background: #000;
} */
#page-prev {
    margin-right: 2vw;
}
#page-next {
    margin-left: 2vw;
}

/* report */
.report-modal,
.delete-modal {
    position: fixed;
    top: 50%;
    left: 50%;
    padding: 4vh 4vw;
    width: 35vw;
    border-radius: 10px;
    background: #fff;
    -webkit-transform: translate(-50%, -50%) scale(0.6);
            transform: translate(-50%, -50%) scale(0.6);
    box-shadow: 0 0 100px rgba(0, 0, 0, 0.15);
    z-index: 1000;
    opacity: 0;
    pointer-events: none;
    transition: all 0.3s;
}
.delete-modal {
    width: 30vw;
}
.report-modal-active,
.delete-modal-active {
    opacity: 1;
    pointer-events: all;
    -webkit-transform: translate(-50%, -50%) scale(1);
            transform: translate(-50%, -50%) scale(1);
}
.report-modal h1,
.delete-modal h1 {
    width: 100%;
    text-align: center;
    margin-bottom: 4vh;
    font-size: 1.6vw;
    font-weight: 500;
    color: #29313d;
}
.report-opts {
    display: flex;
    flex-direction: column;
}
.report-opt {
    display: flex;
    align-items: center;
    margin: 0.8vh 0;
}
.report-opt input {
    width: 1vw;
    height: 1vw;
    cursor: pointer;
    margin: 1vh 0 !important;
}
.report-opt label {
    margin-left: 1vw;
    font-size: 1vw;
}
.report-btns {
    width: 100%;
    display: flex;
    justify-content: space-between;
    align-items: center;
    margin-top: 3vh;
}
.report-close {
    padding: 1.4vh 2vw;
    color: #4678f9;
    border: 1px solid #4678f9;
    font-size: 1vw;
    border-radius: 5px;
    margin-right: 2vw;
    transition: all 0.3s;
}
.report-submit,
.delete-submit {
    padding: 1.4vh 2vw;
    color: #fff;
    background-color: #ff6b6b;
    border: 1px solid #ff6b6b;
    font-size: 1vw;
    border-radius: 5px;
    margin-left: 2vw;
    transition: all 0.3s;
    opacity: 0.7;
    cursor: not-allowed;
    pointer-events: none;
}
.delete-submit {
    opacity: 1;
    cursor: pointer;
}
.delete-modal-active .delete-submit {
    pointer-events: all;
}
.report-submit-proceed {
    opacity: 1;
    cursor: pointer;
    pointer-events: all;
}
.report-close:hover {
    background-color: #4678f9;
    color: #fff;
}
.report-submit-proceed:hover {
    border: 1px solid #cf4444;
    background: #cf4444;
}

.opaq-layer {
    position: fixed;
    top: 0;
    left: 0;
    height: 100vh;
    width: 100vw;
    background: #000000;
    opacity: 0;
    z-index: 1000;
    pointer-events: none;
    transition: all 0.3s;
}
.report-modal-body .opaq-layer {
    opacity: 0.5;
    pointer-events: all;
}
