@media (max-width: 800px) {
    .error-main {
        -webkit-transform: translateY(-10%);
                transform: translateY(-10%);
    }
    .err-img {
        width: 40%;
        margin-bottom: 6vh;
    }
    .error-main h1 {
        font-size: 4vw;
        margin-bottom: 5vh;
    }
    .error-main .hero-btn {
        padding-left: 6vw;
        padding-right: 6vw;
    }
    .error-main .hero-btn img {
        width: 3vw;
    }
    .err-illus {
        justify-content: center;
        bottom: -10%;
    }
    .err-illus img {
        width: 70%;
    }
    #flip-ill {
        display: none;
    }
}
@media (max-width: 500px) {
    .err-img {
        width: 50%;
        margin-bottom: 4vh;
    }
    .error-main h1 {
        font-size: 5vw;
    }
    .error-main .hero-btn {
        padding-left: 8vw;
        padding-right: 8vw;
    }
    .error-main .hero-btn img {
        width: 4vw;
        margin-right: 1vw;
    }
